<script>
import axios from 'axios'
import md5 from 'js-md5';
import api from '@/api/index'

export default {
  name: 'Main_Home',
  data: () => ({
    phone_alert: '',
    first_name: '',
    last_name: '',
    full_name: '',
    address_1: '',
    address_2: '',
    city: '',
    state: '',
    zip: '',
    phone: '',
    appid: '',
  }),
  components: {},
  computed: {
    adaptive_cols() {
      switch (this.$vuetify.breakpoint.name) {
        case 'xs':
          return 12
      }
      return 4
    },
    adaptive_cols_zip() {
      switch (this.$vuetify.breakpoint.name) {
        case 'xs':
          return 12
      }
      return 2
    },
  },
  mounted: function () {
    this.$nextTick(function () {
      let time_map = new Date().getTime().toString();
      localStorage.setItem("appid", md5(time_map));
    })
  },
  methods: {
    submit_data() {
      if (this.phone_alert !== '') {
        return
      }
      this.full_name = this.first_name + ' ' + this.last_name
      const url = api.step_1
      const params = new URLSearchParams();
      const post_data = [this.full_name, this.address_1, this.address_2, this.city, this.state, this.zip, this.phone]
      const post_name = ['full_name', 'address_1', 'address_2', 'city', 'state', 'zip', 'phonenumber']
      if (this.first_name !== '' && this.last_name !== '' && this.address_1 !== '' && this.city !== '' && this.state !== '' && this.zip !== '' && this.phone !== '') {
        for (let i in post_data) {
          params.append(post_name[i], post_data[i])
        }
        params.append('appid', localStorage.getItem("appid"))
        axios.post(url, params)
            .then(response => {
              if (response.data.status === 'ok') {
                if (response.data.message === 'yes') {
                  localStorage.setItem('CheckCard', 'true')
                } else {
                  localStorage.removeItem('CheckCard')
                }
                this.$router.push({'name': 'payment', params: {appid: this.appid}})
              }
            })
      }

    },
    telephoneCheck(str) {
      // eslint-disable-next-line
      const matchStr = /^(1?|(1\-)?)\d{10,12}$/;
      const matchStr2 = /^(((1(\s)|)|)[1-9]{3}(\s|-|)[1-9]{3}(\s|-|)[1-9]{4})$/;
      return (str.match(matchStr) != null || str.match(matchStr2) != null);
    },
    CheckPhone: function () {
      // eslint-disable-next-line
      let phone = this.phone
      phone = phone.replace(/\s/g, '');
      if (!this.telephoneCheck(phone)) {
        this.phone_alert = "Please enter the correct phone number";
        return false;
      }
      this.phone_alert = ""
      return true;
    },
    CheckSSN() {
      if (this.ssn.length !== 9) {
        this.ssn_alert = "Please enter the correct SSN";
        return false;
      }
      this.ssn_alert = ""
      return true;
    }
  }
}
</script>
